body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%);
  color: #222;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  direction: rtl;
}

/* קונטיינרים מרכזיים */
.player-setup,
.finish-screen,
.game-container {
  max-width: 800px;
  width: 100%;
  margin: 40px auto;
  padding: 20px 16px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

/* ריספונסיבי - מובייל */
@media (max-width: 768px) {
  .player-setup,
  .finish-screen,
  .game-container {
    max-width: 95%;
    margin: 20px auto;
    padding: 16px 12px;
  }
}

/* טפסים - הגדרות */
.player-setup form label {
  display: block;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.player-setup input[type="text"] {
  width: 100%;
  padding: 8px;
  font-size: 1rem;
  margin-top: 5px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.player-setup div {
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.player-setup input[type="radio"] {
  margin-left: 10px;
}

/* כפתורים ועיצובים כלליים */
.start-btn,
.restart-btn,
.back-btn,
.finish-btn,
.option-btn,
.speaker-icon {
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  user-select: none;
}

.start-btn,
.restart-btn {
  background: #4caf50;
  color: white;
  font-weight: bold;
  width: 100%;
}

.start-btn:hover,
.restart-btn:hover {
  background: #43a047;
}

.back-btn {
  background: #f44336;
  color: white;
  margin-left: 10px;
  width: 100px;
}

.back-btn:hover {
  background: #d32f2f;
}

/* כפתור סיימתי בכחול בהיר */
.finish-btn {
  background-color: #80c7ff;
  color: white;
  border: none;
  padding: 8px 16px;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 2px 5px rgba(0,0,0,0.15);
  transition: background-color 0.3s ease;
}

.finish-btn:hover {
  background-color: #5aa6e6;
}

/* כפתורי אופציה */
.option-btn {
  background: #eee;
  font-size: 1.3rem;
  padding: 12px;
  border-radius: 12px;
  box-shadow: 0 4px 7px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.option-btn:hover:not(:disabled) {
  background-color: #d0f0fd;
}

.option-btn.correct {
  background-color: #4caf50 !important;
  color: white;
  pointer-events: none;
}

.option-btn.wrong {
  background-color: #f44336 !important;
  color: white;
  pointer-events: none;
}

/* טקסטים והודעות */
.message {
  font-size: 1.1rem;
  margin-top: 10px;
  min-height: 1.5rem;
}

.message.correct {
  color: #4caf50;
  font-weight: 700;
}

.message.wrong {
  color: #f44336;
  font-weight: 700;
}

/* טקסט בהמתנה לטעינה */
.loading-message {
  font-size: 1.3rem;
  font-weight: 600;
  padding: 50px;
}

/* כפתורי סיום במסך הסיום */
.finish-buttons {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 20px;
  flex-wrap: wrap;
}

.finish-buttons button {
  flex: 1;
  max-width: 180px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 8px;
  border: none;
  color: white;
  transition: background-color 0.3s ease;
  min-width: 120px;
}

.finish-buttons button:nth-child(1) {
  background-color: #2ecc71; /* התחלה מחדש */
}

.finish-buttons button:nth-child(1):hover {
  background-color: #27ae60;
}

.finish-buttons button:nth-child(2) {
  background-color: #e67e22; /* חזור למשחק */
}

.finish-buttons button:nth-child(2):hover {
  background-color: #d35400;
}

.finish-buttons .share-btn {
  background-color: #3498db; /* שתף */
}

.finish-buttons .share-btn:hover {
  background-color: #2980b9;
}

.share-btn {
  background-color: #3498db;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.share-btn:hover {
  background-color: #2980b9;
}

/* סקורבורד */
.scoreboard-container {
  max-width: 600px;
  margin: 0 auto 20px;
  padding: 0 16px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.scoreboard-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding: 0 10px;
  direction: rtl;
}

.player-name {
  font-size: 1.3rem;
  font-weight: 600;
  color: #333;
  user-select: none;
}

.scoreboard-stats {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 0 10px;
  font-size: 1.1rem;
  font-weight: 500;
  color: #222;
  direction: rtl;
}

/* שאלות */
.question-container {
  text-align: center;
}

.prompt {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.prompt-text {
  user-select: none;
}

.speaker-icon {
  background: transparent;
  font-size: 2rem;
  line-height: 1;
  padding: 0;
  transition: transform 0.3s ease, color 0.3s ease;
  color: #555;
}

.speaker-icon:hover,
.speaker-icon.hover {
  color: #2196f3;
  transform: scale(1.2);
}

/* ניקוד עם אנימציה */
.score {
  font-size: 1.2rem;
  font-weight: bold;
  transition: transform 0.3s ease;
}

.score.bounce {
  transform: scale(1.3);
}

/* כללי - תיבה ניקוד */
.scoreboard {
  padding: 10px;
}

.scoreboard-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.player-name {
  font-weight: bold;
}

/* מסך סיום */
.finish-screen {
  text-align: center;
  padding: 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  max-width: 500px;
  margin: 40px auto;
}

.trophy-icon {
  font-size: 3rem;
  margin-bottom: 10px;
}

.player-setup,
.finish-screen,
.game-container {
  max-width: 800px;
  width: 100%;
  margin: 40px auto;
  padding: 20px 24px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.options {
  display: flex;
  flex-direction: column;
