.player-setup,
.finish-screen,
.game-container {
  max-width: 900px; /* הגדלתי מ-800 ל-900 */
  width: 100%;
  margin: 40px auto;
  padding: 20px 32px; /* הרחבתי את הפדינג משני הצדדים */
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

/* ריספונסיבי - במובייל נשאיר רווח 5% משני הצדדים */
@media (max-width: 768px) {
  .player-setup,
  .finish-screen,
  .game-container {
    max-width: 95%; /* 95% מרוחב המסך */
    margin: 20px auto;
    padding: 16px 20px; /* קצת פחות פדינג למובייל אבל עדיין רחב */
  }
}
/* כופה רוחב מלא למיכל */
body, #root {
  width: 100vw !important;
  max-width: 100vw !important;
  overflow-x: hidden;
}

.game-container,
.finish-screen,
.player-setup {
  width: 100% !important;
  max-width: 900px !important;
  margin: 40px auto !important;
  padding-left: 40px !important;
  padding-right: 40px !important;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  min-width: 300px; /* למקרה הצר ביותר */
}

/* במובייל, להשאיר רווח מהצדדים */
@media (max-width: 768px) {
  .game-container,
  .finish-screen,
  .player-setup {
    max-width: 95% !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
}
.scoreboard-title {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 16px;
  font-weight: 700;
  color: #333;
}

.scoreboard-stats-row {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  font-weight: 600;
  color: #222;
  padding: 6px 10px;
  border-bottom: 1px solid #ddd;
